import React, { useState } from "react";
import { Tooltip } from "react-tooltip";

const PostInput = ({ onAnalyze }) => {
  const [postUrl, setPostUrl] = useState("");

  const handleAnalyze = (e) => {
    e.preventDefault();
    if (postUrl.trim()) {
      onAnalyze(postUrl);
    }
  };

  return (
    <form className="post-input-form" onSubmit={handleAnalyze}>
      <label>
        URL o ID del post:
        <input
          type="text"
          value={postUrl}
          onChange={(e) => setPostUrl(e.target.value)}
          placeholder="https://hive.blog/@autor/post..."
          data-tooltip-id="post-url-tooltip"
          data-tooltip-content="Pega aqu la URL completa o ID del post que deseas analizar"
        />
        <Tooltip id="post-url-tooltip" place="top" />
      </label>
      <button
        type="submit"
        data-tooltip-id="analyze-btn-tooltip"
        data-tooltip-content="Analiza el contenido del post para asociarlo con tu quema"
      >
        Analizar post
      </button>
      <Tooltip id="analyze-btn-tooltip" place="top" />
    </form>
  );
};

export default PostInput;

